<template>
    <div id="main">
        <div style="float:right;width:50%">
            <mavon-editor :toolbars="toolbars" :subfield="false" :shortCut="false" defaultOpen='preview'
                          v-model="now_total" style="min-height:100vh" @change="changePart" @save="saveHtml" ref="md"/>
        </div>
        <div style="float:left;width:calc(50% - 20px);">
            <div class="page_button">
                <input type="file" accept="text/html" @change="loadPattern($event)">
            </div>
            <div class="page_button button2" @click="downloadPattern">
                
            </div>
            <div class="page_button button3">
                <input type="file" accept=".zip" @change="loadWork($event)">
            </div>
            <cmp :el="pattern_result" :part_data="part_data" :onfocus="onfocus"
                 :changeFocusPosition="changeFocusPosition" style="margin-top:50px"></cmp>
        </div>
    </div>
</template>
<script>
    import autoTextarea from './../components/auto-textarea.vue'
    import Vue from 'vue';
    import JSZip from "jszip";
    import FileSaver from "file-saver";
    import md5 from 'js-md5';

    Vue.component('v-autoTextarea', autoTextarea);

    function setCaretPosition(textDom, pos) {
        if (textDom.setSelectionRange) {
            // IE Support
            textDom.focus();
            textDom.setSelectionRange(pos, pos);
        } else if (textDom.createTextRange) {
            // Firefox support
            let range = textDom.createTextRange();
            range.collapse(true);
            range.moveEnd('character', pos);
            range.moveStart('character', pos);
            range.select();
        }
    }

    function Trim(str) {
        let temp = str.split("\n");
        let result = '';
        for (let i = 0; i < temp.length; i++) {
            result += temp[i].replaceAll(/(^\s*)|(\s*$)/g, "") + '\n';
        }
        return result;
    }

    export default {
        data: () => {
            return {
                pattern: '<!--[code]!-->\n' +
                '<style>\n' +
                '    .title {\n' +
                '        line-height: 130%;\n' +
                '        text-align: center;\n' +
                '        padding: 0.5em 0;\n' +
                '        background-color: #e9e9e9;\n' +
                '        text-shadow: 1px 1px 3px #cccccc;\n' +
                '    }\n' +
                '\n' +
                '    .content {\n' +
                '        width: auto;\n' +
                '        max-width: 960px;\n' +
                '        height: auto !important;\n' +
                '        height: 100%;\n' +
                '        min-height: 100%;\n' +
                '        margin: 0 auto;\n' +
                '        text-align: left;\n' +
                '        /* border: 1px solid #777; */\n' +
                '        background-color: white;\n' +
                '        box-shadow: 0px 1px 5px #999;\n' +
                '        font-family: \'Open Sans\', sans-serif;\n' +
                '        font-size: 10.5pt;\n' +
                '        /* line-height: 2; */\n' +
                '        -webkit-text-size-adjust: none;\n' +
                '        padding-bottom: 15px;\n' +
                '    }\n' +
                '\n' +
                '    .abstract {\n' +
                '        margin: 2em 2em 0.5em 2em;\n' +
                '        line-height: 130%;\n' +
                '    }\n' +
                '\n' +
                '    .title h1 {\n' +
                '        font-size: 24pt;\n' +
                '        line-height: 27pt;\n' +
                '        margin: 10px 0;\n' +
                '        padding-bottom: 0em;\n' +
                '        border: 0;\n' +
                '    }\n' +
                '\n' +
                '    .section h2 {\n' +
                '        margin-top: 0.5em;\n' +
                '        margin-bottom: 0.2em;\n' +
                '        border: 0;\n' +
                '    }\n' +
                '\n' +
                '    .method {\n' +
                '        margin: 2em 2em 0.5em 2em;\n' +
                '        line-height: 130%;\n' +
                '    }\n' +
                '\n' +
                '    .result {\n' +
                '        margin: 2em 2em 0.5em 2em;\n' +
                '        line-height: 130%;\n' +
                '    }\n' +
                '\n' +
                '    .reference {\n' +
                '        margin: 2em 2em 0.5em 2em;\n' +
                '        line-height: 130%;\n' +
                '    }\n' +
                '</style>\n' +
                '<div class=\'content markdown-body\'>\n' +
                '\n' +
                '    <div class=\'title section\'>\n' +
                '\n' +
                '        [code]\n' +
                '\n' +
                '    </div>\n' +
                '    <div class=\'abstract section\'><h2>Abstract</h2><br>\n' +
                '\n' +
                '        [code]\n' +
                '\n' +
                '    </div>\n' +
                '    <div class=\'method section\'><h2>Method</h2><br>\n' +
                '\n' +
                '        [code]\n' +
                '\n' +
                '    </div>\n' +
                '    <div class=\'result section\'><h2>Result</h2><br>\n' +
                '\n' +
                '        [code]\n' +
                '\n' +
                '    </div>\n' +
                '    <div class=\'reference section\'><h2>Reference</h2><br>\n' +
                '\n' +
                '        [code]\n' +
                '\n' +
                '    </div>\n' +
                '</div>',

                pattern_result: '',

                last_total: '',
                now_total: '',

                part_data: {},

                now_focus_id: 0,
                now_focus_position: 0,

                toolbars: {
                    bold: true, // 
                    italic: true, // 
                    header: true, // 
                    underline: true, // 
                    strikethrough: true, // 
                    mark: true, // 
                    superscript: true, // 
                    subscript: true, // 
                    quote: true, // 
                    ol: true, // 
                    ul: true, // 
                    link: true, // 
                    imagelink: true, // 
                    code: true, // code
                    table: true, // 
                    fullscreen: false, // 
                    readmodel: false, // 
                    htmlcode: false, // html
                    help: false, // 
                    /* 1.3.5 */
                    undo: false, // 
                    redo: false, // 
                    trash: false, // 
                    save: true, // eventssave
                    /* 1.4.2 */
                    navigation: false, // 
                    /* 2.1.8 */
                    alignleft: true, // 
                    aligncenter: true, // 
                    alignright: true, // 
                    /* 2.2.1 */
                    subfield: false, // 
                    preview: false, // 
                },
            }
        },
        components: {
            cmp: {
                props: {
                    el: String,
                    part_data: Object,
                    onfocus: Function,
                    changeFocusPosition: Function
                },
                render(h) {
                    const a = Vue.extend({
                        template: this.el,
                        props: {
                            part_data: Object,
                            onfocus: Function,
                            changeFocusPosition: Function
                        }
                    });
                    return h(a, {
                        props: {
                            part_data: this.part_data,
                            onfocus: this.onfocus,
                            changeFocusPosition: this.changeFocusPosition
                        }
                    })
                }
            }
        },
        mounted: function () {
            this.updatePage();
            this.part_data['1'] = '# Project Page Editor\n[Sution^1^](https://gitee.com/scut_Dyb)\n^1^South China University of Technology\n\n503553364@qq.com';
            this.part_data['2'] = '`Project page editor` is based on markdown editor, which is compatible with `markdown` and `HTML` tag syntax. Project page editor divides a complex page into `template part` and `content part` through template rendering. The template part can be imported by users, and the content part will be controlled by multiple input boxes of the page. Users can customize various templates, including setting more `sections` and different `styles` for different sections. Users can fill the content with markdown syntax and HTML tags. The whole process is `simple` and `clear` with `high reusability`.'
            this.part_data['3'] = '### 1. Custom template\n' +
                '\n' +
                '- Download template from this page.\n' +
                '    - Click the button ``, and then it will save a template on your computer.\n' +
                '    - Find this file and open it with text editor, like this:\n' +
                '    ```\n' +
                '        <!--[code]!-->\n' +
                '        <style>\n' +
                '        ...\n' +
                '        </style>\n' +
                '        <div class=\'content markdown-body\'>\n' +
                '\n' +
                '        <div class=\'title section\'>\n' +
                '\n' +
                '        [code]\n' +
                '\n' +
                '        </div>\n' +
                '        <div class=\'abstract section\'><h2>Abstract</h2><br>\n' +
                '\n' +
                '        [code]\n' +
                '\n' +
                '        </div>\n' +
                '        ...\n' +
                '        </div>\n' +
                '\n' +
                '    ```\n' +
                '\n' +
                '- Edit template.\n' +
                '    - In this file, you can set up a new section and style for it, like this:\n' +
                '    ```\n' +
                '         <style>\n' +
                '         .Citation{\n' +
                '             padding:10px;\n' +
                '             line-height:130%;\n' +
                '             border:1px solid black;\n' +
                '         }\n' +
                '         </style>\n' +
                '         <div class=\'content markdown-body\'>\n' +
                '         ...\n' +
                '         <div class=\'Citation section\'><h2>Citation</h2><br>\n' +
                '         \n' +
                '         [code]\n' +
                '         \n' +
                '         </div>\n' +
                '         </div>\n' +
                '    ```\n' +
                '    In this file, `[code]` is a keyword that will be rendered as an input box in the `project page editor`.\n' +
                '\n' +
                '- Upload template.\n' +
                '    - Save file and upload it by the button ``.\n' +
                '\n' +
                '### 2. Save work.\n' +
                '\n' +
                'You can click here:\n' +
                '\n' +
                '![_20210201103405.png](old_0)\n' +
                '\n' +
                'and it will download a zip fileit includes `index.html` which can be displayed directly in the browser, `readme.md` which can be displayed directly on GitHub, and some data files.\n' +
                '\n' +
                '### 3. Continue editing.\n' +
                '\n' +
                'When you want to continue editing the zip file you have saved, you can click the button `` to select the zip file to upload it.\n';
            this.part_data['4'] = '![_20210201104313.png](old_1)\n' +
                '\n' +
                'This `project page editor` is much simple and clear to use and can be rendered in real time.';
            this.part_data['5'] = '- [https://github.com/hinesboy/mavonEditor](https://github.com/hinesboy/mavonEditor)\n' +
                '- [https://github.com/Stuk/jszip](https://github.com/Stuk/jszip)\n' +
                '- [https://github.com/emn178/js-md5](https://github.com/emn178/js-md5)\n' +
                '- [https://github.com/tylingsoft/markdown-it-latex](https://github.com/tylingsoft/markdown-it-latex)\n' +
                '- [https://github.com/eligrey/FileSaver.js](https://github.com/eligrey/FileSaver.js)\n' +
                '- [https://github.com/vuejs/vue](https://github.com/vuejs/vue)\n';
            this.part_data['hidden_resource'] = ["data:image/png;base64,", "data:image/png;base64,"]
        },
        methods: {
            init_data: function () {
                this.pattern_result = '';
                this.last_total = '';
                this.now_total = '';
                this.part_data = {};
                this.now_focus_id = 0;
                this.now_focus_position = 0;
            },
            updatePage: function () {
                this.init_data();
                // replace [code] to <v-autoTextarea>
                // replace <style></style> to ''
                this.pattern = Trim(this.pattern);
                let style_pattern = /<style([^>]*?)>([^<]*?)<\/style>/g;
                let temp = this.pattern.replaceAll(style_pattern, '');
                let reg_pattern = '[code]';
                temp = temp.split(reg_pattern);
                let count = temp.length - 1;
                let result = temp[0];
                this.$set(this.part_data, '0', '');
                for (let i = 0; i < count; i++) {
                    result += '<v-autoTextarea' +
                        '      v-model="part_data[\'' + i.toString() + '\']" @onfocus="onfocus($event)" id="' + i.toString() + '" :onchange="changeFocusPosition"' +
                        '      style="width:100%;box-shadow: rgba(0, 0, 0, 0.1) 0 2px 12px 0;min-height:200px"/>' + temp[i + 1];
                    this.$set(this.part_data, i.toString(), '');
                }
                this.pattern_result = result;
                this.$nextTick(() => {
                    this.updateTotal();
                });
            },
            changePart: function (value, render) {
                let change_part = value.replaceAll(this.last_total, '');
                if (change_part === '') {
                    return;
                }
                this.part_data[this.now_focus_id.toString()] = this.part_data[this.now_focus_id.toString()].slice(0, this.now_focus_position) + change_part + this.part_data[this.now_focus_id.toString()].slice(this.now_focus_position, this.part_data[this.now_focus_id.toString()].length);

                this.$nextTick(() => {
                    this.now_focus_position += change_part.length;
                    this.updateTotal();
                    const dom = document.getElementById(this.now_focus_id.toString()).children[1];
                    setCaretPosition(dom, this.now_focus_position);
                });
            },
            onfocus: function (event) {
                this.$nextTick(() => {
                    this.now_focus_id = parseInt(event.id);
                    this.now_focus_position = event.children[1].selectionStart;
                });
            },
            changeFocusPosition: function (temp_value, event) {
                this.now_focus_id = parseInt(event.id);
                this.now_focus_position = event.children[1].selectionStart;
                this.updateTotal();
            },
            render_old_resource: function (part_data, old_resource = null) {
                if (Object.keys(this.part_data).indexOf('hidden_resource') !== -1) {
                    if (old_resource == null) {
                        old_resource = this.part_data['hidden_resource'];
                    }
                    for (let i = 0; i < old_resource.length; i++) {
                        const pattern = new RegExp('\\!\\[(.*?)\\]\\(old_' + i.toString() + '\\)', 'g');
                        const result = [...part_data.matchAll(pattern)];
                        for (let j = 0; j < result.length; j++) {
                            const replace_pattern = '![' + result[j][1] + '](old_' + i.toString() + ')';
                            const replace_as_pattern = '![' + result[j][1] + '](' + old_resource[i] + ')';
                            part_data = part_data.replaceAll(replace_pattern, replace_as_pattern);
                        }
                    }
                }
                return part_data;
            },
            updateTotal: function () {
                let template = '';
                let temp = this.pattern.split('[code]');
                for (let i in this.part_data) {
                    if (i === 'hidden_resource') continue;
                    template += temp[parseInt(i)] + this.render_old_resource(this.part_data[i]);
                }
                template += temp[temp.length - 1];
                this.last_total = template;
                this.now_total = this.last_total;
            },
            loadPattern: function (event) {
                if (event.target.files.length === 0) {
                    return;
                }
                let pattern_file = event.target.files[0];

                let reader = new FileReader();
                reader.onload = () => {
                    if (reader.result) {
                        //
                        if (reader.result !== this.pattern) {
                            this.pattern = reader.result;
                            this.updatePage();
                        }
                    }
                };
                reader.readAsText(pattern_file);
            },
            downloadPattern: function () {
                FileSaver.saveAs(new File([this.pattern], 'pattern.html', {type: 'text/pain;charset=utf-8'}));
                return;
            },
            loadWork: function (event) {
                if (event.target.files.length === 0) {
                    return;
                }
                let work_file = event.target.files[0];
                let new_zip = new JSZip();
                let self = this;
                new_zip.loadAsync(work_file)
                    .then(function (file) {
                        if (Object.keys(file.files).indexOf('lock.txt') === -1) {
                            alert('lock.txt');
                            return false;
                        }
                        file.files['lock.txt'].async('string').then(lock_content => {
                            const file_names = lock_content.split('\n');
                            const lock_md5 = file_names[file_names.length - 1];
                            let total_temp = '';
                            let pattern_temp = '';
                            let value_temp = '';
                            let promise_list = [];
                            for (let i = 0; i < file_names.length - 1; i++) {
                                const file_name = file_names[i];
                                if (Object.keys(file.files).indexOf(file_name) === -1) {
                                    alert('' + file_name);
                                    return false;
                                }
                                if (file_name === 'pattern.html') {
                                    let p = file.files[file_name].async('string');
                                    p.then(res => {
                                        pattern_temp = res;
                                    });
                                    promise_list.push(p);
                                }
                                if (file_name === 'value.txt') {
                                    let p = file.files[file_name].async('string');
                                    p.then(res => {
                                        value_temp = res;
                                    });
                                    promise_list.push(p);
                                }
                            }
                            Promise.all(promise_list).then(res => {
                                total_temp = pattern_temp + value_temp;
                                if (md5(total_temp) !== lock_md5) {
                                    alert('');
                                    return false;
                                }
                                self.pattern = pattern_temp;
                                self.updatePage();
                                self.part_data = JSON.parse(value_temp);
                                self.updateTotal();
                                return true;
                            });
                        });
                    })
            },
            transfer_markdown_pattern: function (template) {
                template = template.replaceAll(/\^(.*?)\^/g, '<sup>$1</sup>');
                template = template.replaceAll(/~([^~]+?)~/g, '<sub>$1</sub>');
                template = template.replaceAll(/~<sub>([^<]+?)<\/sub>~/g, '~$1~');
                template = template.replaceAll(/\+\+(.*?)\+\+/g, '<u>$1</u>');
                return template;
            },
            saveHtml: function (val, render) {
                const zip = new JSZip();
                let temp = '<head><meta charset="UTF-8"><link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/github-markdown-css/2.9.0/github-markdown.min.css"><link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/KaTeX/0.8.3/katex.min.css"><script type="text/javascript" charset="UTF-8" src="https://cdnjs.cloudflare.com/ajax/libs/KaTeX/0.8.3/katex.min.js"><\/script>\n' +
                    '<script type="text/javascript" charset="UTF-8" src="https://cdnjs.cloudflare.com/ajax/libs/highlight.js/9.12.0/highlight.min.js"><\/script>\n' +
                    '<link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/highlight.js/9.12.0/styles/github.min.css" id="md-code-style"><\/head>';

                temp += render;
                // 2\ ![.*?](index)
                // 1\this.$refs.md.$refs.toolbar_left.img_fileindexminiurl
                // 3\index ![.*?](old_{index})
                // 4\value.txthidden_resources

                // hidden_resource
                let part_data_copy = {};
                Object.assign(part_data_copy, this.part_data);
                let temp_hidden_resource = [];
                if (Object.keys(part_data_copy).indexOf('hidden_resource') !== -1) {
                    for (let i = 0; i < part_data_copy['hidden_resource'].length; i++) {
                        const temp_pattern = new RegExp('\\!\\[(.*?)\\]\\(old_' + i.toString() + '\\)', 'g');
                        let state = false;
                        for (let j in part_data_copy) {
                            if (j === 'hidden_resource') continue;
                            const temp_value = part_data_copy[j];
                            if (temp_value.match(temp_pattern) !== null) {
                                state = true;
                            }
                        }
                        if (state) {
                            temp_hidden_resource.push(part_data_copy['hidden_resource'][i]);
                            for (let j in part_data_copy) {
                                if (j === 'hidden_resource') continue;
                                const temp_value = part_data_copy[j];
                                const match_result = [...temp_value.matchAll(temp_pattern)];
                                for (let k = 0; k < match_result.length; k++) {
                                    const replace_pattern = '![' + match_result[k][1] + '](old_' + i.toString() + ')';
                                    const replace_as_pattern = '![' + match_result[k][1] + '](old_' + (temp_hidden_resource.length - 1).toString() + ')';
                                    part_data_copy[j] = part_data_copy[j].replaceAll(replace_pattern, replace_as_pattern);
                                }
                            }
                        }
                    }
                }
                let hidden_resource_top_index = temp_hidden_resource.length;
                const current_page_image_files = this.$refs.md.$refs.toolbar_left.img_file;
                for (let i = 1; i < current_page_image_files.length; i++) {
                    const img_data = current_page_image_files[i][1].miniurl;
                    // check this.pattern_data['hidden_resource']
                    const temp_pattern = new RegExp('\\!\\[(.*?)\\]\\(' + i.toString() + '\\)', 'g');
                    let state = false;
                    for (let j in part_data_copy) {
                        if (j === 'hidden_resource') continue;
                        const temp_value = part_data_copy[j];
                        if (temp_value.match(temp_pattern) !== null) {
                            state = true;
                        }
                    }
                    if (state) {
                        temp_hidden_resource.push(img_data);
                        for (let j in part_data_copy) {
                            if (j === 'hidden_resource') continue;
                            const temp_value = part_data_copy[j];
                            const match_result = [...temp_value.matchAll(temp_pattern)];
                            for (let k = 0; k < match_result.length; k++) {
                                const replace_pattern = '![' + match_result[k][1] + '](' + i.toString() + ')';
                                const replace_as_pattern = '![' + match_result[k][1] + '](old_' + hidden_resource_top_index.toString() + ')';
                                part_data_copy[j] = part_data_copy[j].replaceAll(replace_pattern, replace_as_pattern);
                            }
                        }
                        hidden_resource_top_index += 1;
                    }
                }
                part_data_copy['hidden_resource'] = temp_hidden_resource;

                let template = '';
                let temp_split = this.pattern.split('[code]');
                for (let i in part_data_copy) {
                    if (i === 'hidden_resource') continue;
                    template += temp_split[parseInt(i)] + this.render_old_resource(part_data_copy[i], temp_hidden_resource);
                }
                template += temp_split[temp_split.length - 1];
                template = this.transfer_markdown_pattern(template);

                zip.file('pattern.html', this.pattern, {binary: false});
                zip.file('index.html', temp, {binary: false});
                zip.file('value.txt', JSON.stringify(part_data_copy), {binary: false});
                zip.file('readme.md', template.replaceAll(/<style([^>]*?)>([^<]*?)<\/style>/g, ''), {binary: false});
                const md5_txt = md5(this.pattern + JSON.stringify(part_data_copy));
                const file_structure = 'pattern.html\nvalue.txt\n';
                zip.file('lock.txt', file_structure + md5_txt, {binary: false});
                zip.generateAsync({type: 'blob'}).then(content => {
                    FileSaver.saveAs(content, 'ProjectPage.zip');
                })
            }
        }
    }
</script>
<style>
    .page_button {
        position: absolute;
        background: #e0e0e0;
        color: #606266;
        padding: 8px;
        font-size: 14px;
        border: 1px solid #ebeef5;
        border-radius: 4px;
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        cursor: pointer;
    }

    .page_button input {
        opacity: 0;
        right: 0;
        top: 0;
        position: absolute;
        width: 100%;
        height: 100%;
        cursor: pointer;
    }

    .page_button:hover {
        background: #b0b0b0;
    }

    .button2 {
        margin-left: 150px;
    }

    .button3 {
        margin-left: 250px;
    }

</style>