<template>
    <div :style="{fontSize: fontSize , lineHeight: lineHeight, height: fullHeight ? '100%': 'auto'}"
         class="auto-textarea-wrapper">
        <pre :style="{fontSize: fontSize , lineHeight: lineHeight, minHeight: fullHeight ? '100%': 'auto'}"
             class="auto-textarea-block"><br/>{{temp_value}} </pre>
        <textarea ref="vTextarea" :autofocus="s_autofocus" spellcheck="false"
                  :placeholder="placeholder"
                  v-model="temp_value" :style="{fontSize: fontSize , lineHeight: lineHeight}"
                  :class="{'no-border': !border , 'no-resize': !resize}" class="auto-textarea-input"
                  @focus="onfocus"
        >
      </textarea>
    </div>
</template>

<script>
    export default {
        name: 'autoTextarea',
        data() {
            return {
                temp_value: (() => {
                    return this.value;
                })(),
                s_autofocus: (() => {
                    if (this.autofocus) {
                        return 'autofocus'
                    }
                })()
            };
        },
        mounted() {
        },
        props: {
            fullHeight: {
                type: Boolean,
                default: false
            },
            autofocus: {
                type: Boolean,
                default: false
            },
            value: {
                type: String,
                default: ''
            },
            placeholder: {
                type: String,
                default: ''
            },
            border: {
                type: Boolean,
                default: false
            },
            resize: {
                type: Boolean,
                default: false
            },
            onchange: {
                type: Function,
                default: null
            },
            fontSize: {
                type: String,
                default: '14px'
            },
            lineHeight: {
                type: String,
                default: '18px'
            }
        },
        methods: {
            onfocus() {
                this.$emit('onfocus', this.$el);
            }
        },
        watch: {
            value: function (val, oldVal) {
                this.temp_value = val;
            },
            temp_value: function (val, oldVal) {
                this.$emit('input', val);
                if (this.onchange) {
                    this.onchange(val, this.$el);
                }
            },
            deep: true
        },

    };
</script>
<style>
    .auto-textarea-wrapper {
        position: relative;
        width: 100%;
        margin: 0;
        padding: 0;
        line-height: normal;
    }

    .auto-textarea-wrapper > .auto-textarea-block {
        display: block;
        white-space: pre-wrap;
        word-wrap: break-word !important;
        visibility: hidden;
        overflow: hidden;
        margin: 0;
        padding: 0;
        box-sizing: border-box;
        font-size: 100%;
    }

    .auto-textarea-wrapper > .auto-textarea-input {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        margin: 0;
        padding: 0;
        overflow-y: hidden;
        color: #2C3E50;
        background-color: rgb(251, 251, 251)
    }

    .no-border {
        outline: 0 none;
        border: none !important
    }

    .no-resize {
        resize: none;
    }
</style>
